import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from "@angular/router";

import { ChartsModule } from 'ng2-charts';

import { SmsService } from "./SMS.service";
//Popover
import { PopoverModule } from 'ng2-popover';

//Spinner
import { NgSpinKitModule } from 'ng-spin-kit'

//Captcha
import { ReCaptchaModule } from 'angular2-recaptcha/angular2-recaptcha';

//DatePicker
import { MyDatePickerModule } from 'mydatepicker';

//Multiselect
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown/angular2-multiselect-dropdown';

import { AppComponent } from './app.component';
import { LoginComponent } from "./Login/login.component";
import { PostLoginComponent } from "./Login/postlogin.component";

import { LeftPanelComponent } from "./LeftPanel/leftpanel.component";
import { TopPanelComponent } from "./TopPanel/toppanel.component";
import { FooterComponent } from "./Footer/footer.component";
import { MainViewComponent } from "./MainView/mainview.component";

import { AuthGuard } from "./Authenticate/auth.service";
import { AlertService } from "./services/alert.service";
import { AuthenticationService } from "./services/authentication.service";
import { ForgotPasswordComponent } from "./Login/forgotpassword.component";

//Lending Page
// import { LendingComponent } from "./Lending/lending.component";

import { SetPasswordComponent } from "./Login/setpassword.component";
//Pipe
import { DemoNumber } from "./MainView/Pipe/page.pipe";
import { OrderByPipe } from "./MainView/Pipe/OrderByPipe";
//MainView
import { StateComponent } from "./MainView/State/state.component";

// Employee Login
import { EmailUploadComponent } from "./UploadEmails/email-upload.component";
import { ChangePasswordComponent } from "./MainView/ChangePassword/changepassword.component";
import { DashboardOneComponent } from './MainView/dashboard-one/dashboard-one.component';
import { DashboardTwoComponent } from './MainView/dashboard-two/dashboard-two.component';

@NgModule({
  declarations: [
    SetPasswordComponent,
    AppComponent,
    LoginComponent,
    PostLoginComponent,
    LeftPanelComponent,
    TopPanelComponent,
    FooterComponent,
    MainViewComponent,
    ForgotPasswordComponent,

    //Pipe
    DemoNumber,
    OrderByPipe,

    //MainView
    StateComponent,
    ChangePasswordComponent,

    // Employee
    EmailUploadComponent,

    DashboardOneComponent,

    DashboardTwoComponent,

  ],
  imports: [
    ReCaptchaModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    AngularMultiSelectModule,
    MyDatePickerModule,
    ChartsModule,
    PopoverModule,
    NgSpinKitModule,
    RouterModule.forRoot([
      {
        path: '',
        component: LoginComponent
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'postlogin',
        component: PostLoginComponent
      },
      {
        path: 'email-upload',
        component: EmailUploadComponent
      },
      {
        path: 'home',
        component: MainViewComponent,
        children: [
          {
            path: '',
            redirectTo: 'email-upload',
            pathMatch: 'full'
          },
          //MainView 
          { path: 'state', component: StateComponent },
          { path: 'dashboardOne', component: DashboardOneComponent },
          { path: 'dashboardTwo', component: DashboardTwoComponent },
          { path: 'changepassword', component: ChangePasswordComponent, canActivate: [AuthGuard] },
        ]
      },
      { path: 'login', component: LoginComponent },
      { path: 'forgot-password', component: ForgotPasswordComponent },
      { path: 'newpassword', component: SetPasswordComponent },
      { path: '**', redirectTo: ' ' }
    ])
  ],
  providers: [SmsService, AuthGuard, AlertService, AuthenticationService],
  bootstrap: [AppComponent]
})
export class AppModule { }



