import { Router, ActivatedRoute } from "@angular/router";

import { Component, OnInit } from '@angular/core';
import { SmsService } from "../SMS.service";
import { ForgotPassword } from "./forgotpassword.model";
@Component({
  moduleId: module.id,
  selector: 'setpassword',
  templateUrl: 'setpassword.component.html'
})
export class SetPasswordComponent implements OnInit {

  constructor(private service: SmsService, private router: Router,
    private route: ActivatedRoute) { }
  model: ForgotPassword = new ForgotPassword();

  ngOnInit() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.add("gray-bg");

    this.model.token = this.route.snapshot.queryParams['token'];
  }

  ngOnDestroy() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove("gray-bg");
  }

  forgotSuccess: boolean = false;
  forgotError: boolean = false;

  forgot() {
    this.service.post('v1/auth/changepassword', this.model).subscribe(response => {
      if (response) {
        this.forgotSuccess = true;
        this.forgotError = false;
        this.model = new ForgotPassword();
        this.router.navigate(['login']);
      } else {
        this.forgotError = true;
        this.forgotSuccess = false;
      }
    });
  }
}



