import { Component, OnInit } from "@angular/core";
import { SmsService } from "../../SMS.service";
import { State } from "./state.model";
import { Constant } from "../../constant";
declare var jQuery: any, toastr: any;

@Component({
    moduleId: module.id,
    selector: 'state',
    templateUrl: 'state.component.html'
})
export class StateComponent implements OnInit {

    context: string = "state";
    response: any = {};

    dropdownSettings = Constant.dropdownSettings;
    countryResponse: Array<any> = [];
    selectedCountry: Array<any> = [];

    modal: State = new State();

    loader: boolean = false;

    constructor(private service: SmsService) { }

    ngOnInit() {
        this.service.get("country/all").subscribe(response => {
            this.countryResponse = this.service.convertArrayForMultiselect(response);
        });
        this.loadGrid();
    }

    loadGrid(size?: number, page?: number) {
        this.loader = true;
        this.service.get(this.context, size, page).subscribe(response => {
            this.response = response;
            this.loader = false;
        });
    }

    // -------------------------------------------------------------------------

    // manageCountry(isSelect, item: any) {
    //     if (isSelect) {
    //         this.modal.country.id = item["id"];
    //     } else {
    //         this.modal.country.id = 0;
    //     }
    // }

    // -------------------------------------------------------------------------

    getById(id) {
        this.service.getById(this.context, id).subscribe(response => {
            this.modal = response;
            this.selectedCountry = this.service.convertForMultiselect(response.country);
            console.log(this.selectedCountry);
        });
    }
    search(value) {
        this.isSearch = true;
        this.loader = true;
        this.service.get(this.context + "/search?q=" + value).subscribe(response => {
            this.response = response;
            this.loader = false;
        });
    }

    isSearch: boolean = false;
    searchState: any;

    resetSearch() {
        this.searchState = "";
        this.isSearch = false;
        this.loadGrid();
    }

    resetForm() {
        this.selectedCountry = [];
        this.modal = new State();
    }
    save(id) {
        this.loader = true;
        if (id == "1") {
            this.service.postNew(this.context, this.modal).subscribe(response => {
                toastr.success('State saved successfully');
                this.loadGrid();
                this.loader = false;
                this.resetForm();
            });
        } else {
            this.service.postNew(this.context, this.modal).subscribe(response => {
                jQuery('#stateModel').modal('hide');
                toastr.success('State saved successfully');
                this.loadGrid();
                this.loader = false;
                this.resetForm();
            });
        }
    }

    // Grid Functions

    getByEntries(grid_entries) {
        this.loadGrid(grid_entries.value);
    }

    jumpToPage(grid_entries, grid_page) {
        this.loadGrid(grid_entries.value, grid_page.value - 1);
    }

    prevPage(grid_entries, grid_page) {
        grid_page.value = parseInt(grid_page.value) - 1;
        this.jumpToPage(grid_entries, grid_page);
    }

    nextPage(grid_entries, grid_page) {
        grid_page.value = parseInt(grid_page.value) + 1;
        this.jumpToPage(grid_entries, grid_page);
    }
}


