import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { SmsService } from "../SMS.service";
import { AuthenticationService } from "../services/authentication.service";
import { DomSanitizer } from '@angular/platform-browser';
declare var jQuery: any, toastr: any;

@Component({
  selector: 'postlogin',
  templateUrl: './postlogin.component.html',
  styles: [`
      #set_back {
        background-image: url(/img/bgimg.jpg);
        background-size: cover;
        background-repeat: no-repeat;height: 100%;
      }
      @media (max-width:500px) {
        #set_back {height: auto;
      }
    } 
    .cup{
      width:20%;
      display:inline-block;

    } 
    @media screen and (max-width:767px){
      .cup{
        display:block;
        width:100%;
        
      }
    }
  `]
})
export class PostLoginComponent implements OnInit {
  title = 'app works!';

  constructor(private router: Router, private service: SmsService, private authenticationService: AuthenticationService, private domSanitizer: DomSanitizer) { }

  userName: string = "";

  loader: boolean = false;
  imageResponse: Array<any> = [];

  ngOnInit() {
    this.userName = localStorage.getItem("userName");
    this.service.get("user/employee/images").subscribe(response => {
      this.imageResponse = response;

    })
    console.log(this.imageResponse);
  }

  //Base64
  files: Array<File> = [];
  disableAdd = false;
  enableSubmit = true;

  private base64Image: Array<string> = [];

  changeListener($event): void {
    var counter = 10 - this.files.length;
    if (counter > $event.target.files.length) {
      counter = $event.target.files.length;
    }
    for (var i = 0; i < counter; i++) {
      if (this.files.length != 10) {
        this.readThis($event.target.files[i]);
      }
    }
  }

  readThis(file: File): void {
    var myReader: FileReader = new FileReader();
    let imageName = file.name;
    // myReader.onloadend = (e) => {


    //   this.files.push(file);
    //   this.base64Image.push(myReader.result);
    //   if (this.files.length >= 10) {
    //     this.disableAdd = true;
    //     this.enableSubmit = false;
    //   }

    // }

    myReader.onload = function (readerEvent) {
      var image = new Image();
      var resizedImage;
      image.onload = function (imageEvent) {

        // Resize the image
        var canvas = document.createElement('canvas'),
          max_size = 544,// TODO : pull max size from a site config
          width = image.width,
          height = image.height;

        if (width > height) {
          if (width > max_size) {
            height *= max_size / width;
            width = max_size;
          }
        } else {
          if (height > max_size) {
            width *= max_size / height;
            height = max_size;
          }
        }
        canvas.width = 400;
        canvas.height = 400;
        var ctx = canvas.getContext('2d');
        ctx.fillStyle = "white";
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(image, 0, 0, 400, 400);
        var dataUrl = canvas.toDataURL('image/jpeg');
        resizedImage = this.dataURLToBlob(dataUrl);
        var file1 = this.dataURLtoFile(dataUrl, imageName);
        this.files.push(file1);
        this.base64Image.push(dataUrl);
        if (this.files.length >= 10) {
          this.disableAdd = true;
          this.enableSubmit = false;
        }

      }.bind(this);
      image.src = readerEvent.target.result;
    }.bind(this);

    myReader.readAsDataURL(file);
  }

  Remove(index) {
    this.disableAdd = false;
    this.enableSubmit = true;
    this.files.splice(index, 1);
    this.base64Image.splice(index, 1);
  }

  fileRes: any;
  save() {
    this.loader = true;
    console.log(this.files);
    this.service.postFiles("user/employee/images", this.files)
      .map(res => this.fileRes = res)
      .subscribe(response => {
        toastr.success("Images Successfuly uploaded...");
        this.loader = false;
        this.files = [];
        this.disableAdd = false;
        this.enableSubmit = true;
        this.router.navigate(['employee']);
      });
  }

  dataURLToBlob(dataURL) {
    var BASE64_MARKER = ';base64,';
    if (dataURL.indexOf(BASE64_MARKER) == -1) {
      var parts = dataURL.split(',');
      var contentType = parts[0].split(':')[1];
      var raw1 = parts[1];

      return new Blob([raw1], { type: contentType });
    }

    var parts = dataURL.split(BASE64_MARKER);
    var contentType = parts[0].split(':')[1];
    var raw = window.atob(parts[1]);
    var rawLength = raw.length;

    var uInt8Array = new Uint8Array(rawLength);

    for (var i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }


    return new Blob([uInt8Array], { type: contentType });
  }


  dataURLtoFile(dataurl, filename) {
    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, { type: mime });
  }


}




