import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { Login } from "./login.model";
import { SmsService } from "../SMS.service";
import { AuthenticationService } from "../services/authentication.service";
import { AlertService } from "../services/alert.service";
@Component({
  moduleId: module.id,
  selector: 'login',
  templateUrl: 'login.component.html',
  styleUrls: ['login.component.css']

})
export class LoginComponent implements OnInit {
  title = 'app works!';

  modal: Login = new Login();
  response: Array<any> = [];
  showCaptcha = false;
  captchaVerified = false;

  i: number = 0;

  loading = false;
  returnUrl: string;

  loader: boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private service: SmsService,
    private authenticationService: AuthenticationService,
    private alertService: AlertService
  ) { }

  ngOnInit() {

    let body = document.getElementsByTagName('body')[0];
    body.classList.add("gray-bg");

    // reset login status
    this.authenticationService.logout();

    // get return url from route parameters or default to '/'
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
  }

  ngOnDestroy() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove("gray-bg");
  }

  loginFailed = false;

  loginTest() {
    let token = btoa(this.modal.userName + ":" + this.modal.password);
    localStorage.setItem("Authorization", token);
    this.service.getIPG("getAllDashboardReportTask").subscribe(response => {
      this.response = response;
      this.router.navigate(['email-upload']);
    });
  }

  // login() {
  //   this.loader = true;
  //   this.service.login('v1/auth/login', this.modal).subscribe(response => {
  //     this.response = response;
  //     if (response.success) {
  //       localStorage.setItem("Authorization", response["data"]["token"]);
  //       localStorage.setItem("userName", response["data"]["user"]["name"]);
  //       localStorage.setItem("role", response["data"]["user"]["role"]);
  //       localStorage.setItem("userLastSynced", response["data"]["userLastSynced"]);

  //       if (response["data"]["user"]["role"] == 'ROLE_USER') {
  //         if (response.data.imageCount >= 10) {
  //           this.router.navigate(['employee']);
  //         } else {
  //           this.router.navigate(['postlogin']);
  //         }
  //       } else {
  //         this.router.navigate(['home/dashboard']);
  //       }
  //       this.loader = false;
  //     } else {
  //       this.loginFailed = true;
  //       this.loader = false;
  //     }
  //   });
  // }
}



