import { Injectable } from '@angular/core';
import { IMyDpOptions, IMyDateModel } from 'mydatepicker';

@Injectable()
export class Constant {
    static defaultProfilePic: string = "data:image/png;base64,";

    static defaultSignaturePic = "data:image/png;base64,";

    static dropdownSettings = {
        singleSelection: true,
        text: "Select",
        selectAllText: 'Select All',
        unSelectAllText: 'UnSelect All',
        enableSearchFilter: true,
        classes: "customDD",
        badgeShowLimit: 1,
        maxHeight: 200
    };

    static dropdownSettingsForMessaging = {
        singleSelection: true,
        text: "Select Template",
        selectAllText: 'Select All',
        unSelectAllText: 'UnSelect All',
        enableSearchFilter: true,
        classes: "customDD",
        badgeShowLimit: 1,
        maxHeight: 200
    };

    static dropdownSettings2 = {
        singleSelection: false,
        text: "Select",
        selectAllText: 'Select All',
        unSelectAllText: 'UnSelect All',
        enableSearchFilter: true,
        classes: "customDD",
        badgeShowLimit: 1,
        maxHeight: 200,
    };

    static dropdownSettings3 = {
        singleSelection: true,
        text: "Select",
        selectAllText: 'Select All',
        unSelectAllText: 'UnSelect All',
        enableSearchFilter: true,
        classes: "customDD",
        badgeShowLimit: 1,
        maxHeight: 200,
        groupBy: "category"
    };

    static dropdownSettings4 = {
        singleSelection: false,
        text: "All",
        selectAllText: 'Select All',
        unSelectAllText: 'UnSelect All',
        enableSearchFilter: true,
        classes: "customDD",
        badgeShowLimit: 1,
        maxHeight: 200,
    };

    static myDatePickerOptionsWithDisable: IMyDpOptions = {
        dateFormat: 'dd/mm/yyyy HH:mm:ss',
        markCurrentDay: true,
        editableDateField: false,
        disableSince: { year: +(new Date()).getFullYear(), month: (new Date()).getMonth() + 1, day: (new Date()).getDate() + 1 }
    };

    static myDatePickerOptions: IMyDpOptions = {
        dateFormat: 'dd/mm/yyyy',
        markCurrentDay: true,
        editableDateField: false
    };
}




