import { Injectable, ElementRef } from "@angular/core";
import { Http, Response, Headers, RequestOptions, ResponseContentType } from "@angular/http";
import 'rxjs/add/operator/toPromise';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';

import { saveAs } from 'file-saver';

declare var jQuery: any, toastr: any;

@Injectable()
export class SmsService {

    baseUrl: string = "http://ipgmediabrand-emailapp.dotin.us";
    // baseUrl: string = "https://jsonplaceholder.typicode.com";
    SLASH: string = "/";

    constructor(private http: Http) {
        // this.baseUrl = location.origin;
    }

    getIPG(url: any): Observable<any> {
        let token = localStorage.getItem('Authorization');
        let headers: Headers = new Headers();
        headers.append("Authorization", "Basic " + token);
        return this.http.get(this.baseUrl + this.SLASH + url, { headers: headers }).map((response: Response) => response.json());
    }

    cleanDirtyRequest(model: any) {
        for (var key in model) {
            if (model.hasOwnProperty(key) && model[key] != null && typeof model[key] == "object") {
                if (model[key].hasOwnProperty("id") && (model[key]["id"] == 0 || model[key]["id"] == undefined)) {
                    model[key] = null;
                }
            }
        }
    }

    login(url: string, username: any, password: any, size?: number, page?: number) {

        let headers: Headers = new Headers();
        headers.append("Authorization", "Basic " + btoa(username + ":" + password));
        headers.append("Content-Type", "application/x-www-form-urlencoded");
        // return this.http.post(this.baseUrl + this.SLASH + url, model, { headers: headers })
        // return this.http.post(this.baseUrl + this.SLASH + url, model).map((response: Response) => response.json());
    }

    getById(context: string, id: number) {
        return this.http.get(this.baseUrl + this.SLASH + context + this.SLASH + id, this.createAuthorizationHeader()).map(response => response.json());
    }
    getBySize(context: string, size: number) {
        return this.http.get(this.baseUrl + this.SLASH + context + '?size=' + size, this.createAuthorizationHeader()).map(response => response.json());
    }

    loadMaster(context: string) {
        return this.http.get(this.baseUrl + this.SLASH + context, this.createAuthorizationHeader()).map((response: Response) => response.json());
    }

    loadMasterBy(context: string, id: number) {
        return this.http.get(this.baseUrl + this.SLASH + context + this.SLASH + id, this.createAuthorizationHeader()).map((response: Response) => response.json());
    }

    // Grid
    getByPage(context: string, page: number, size: number) {
        return this.http.get(this.baseUrl + this.SLASH + context + '?page=' + page + '&size=' + size, this.createAuthorizationHeader()).map(response => response.json());
    }

    // Generic Get 

    getClean(url: string, size?: number, page?: number) {
        let paging = "";
        let join = "?";
        if (url.indexOf("?") != -1) {
            join = "&"
        }

        if (size != undefined) {
            paging += join + "size=" + size;
        }

        if (page != undefined) {
            paging += "&page=" + page;
        }

        let headers = new Headers();
        return this.http.get(this.baseUrl + this.SLASH + url + paging).map((response: Response) => response.json());
    }

    get(url: string, size?: number, page?: number) {
        let paging = "";
        let join = "?";
        if (url.indexOf("?") != -1) {
            join = "&"
        }

        if (size != undefined) {
            paging += join + "size=" + size;
        }

        if (page != undefined) {
            paging += "&page=" + page;
        }

        let headers = new Headers();
        return this.http.get(this.baseUrl + this.SLASH + url + paging, this.createAuthorizationHeader()).map((response: Response) => response.json());
    }

    getNew(url: string) {
        let headers = new Headers();
        return this.http.get(this.baseUrl + this.SLASH + url, this.createAuthorizationHeader()).map(response => response);
    }

    getAll(context: string): Observable<any> {
        return this.http.get(this.baseUrl + this.SLASH + context, this.createAuthorizationHeader()).map((response: Response) => response.json());
    }

    postNew(url: string, model: any) {
        // this.cleanDirtyRequest(model);
        return this.http.post(this.baseUrl + this.SLASH + url, model, this.createAuthorizationHeader()).map(response => response);
    }

    post(url: string, model: any, size?: number, page?: number) {
        let paging = "";
        if (size != undefined) {
            paging += "?size=" + size;
        }

        if (page != undefined) {
            paging += "&page=" + page;
        }
        // this.cleanDirtyRequest(model);
        return this.http.post(this.baseUrl + this.SLASH + url, model, this.createAuthorizationHeader()).map((response: Response) => response.json());
    }

    put(url: string, model: any) {
        return this.http.put(this.baseUrl + this.SLASH + url, model, this.createAuthorizationHeader()).map(response => response);
    }

    delete(url: string) {
        return this.http.delete(this.baseUrl + this.SLASH + url, this.createAuthorizationHeader()).map((response: Response) => response.json());
    }

    postWithFile(url: string, file: any, model: any) {

        return Observable.fromPromise(new Promise((resolve, reject) => {

            var formData: FormData = new FormData();

            this.cleanDirtyRequest(model);
            var payload = new Blob([JSON.stringify(model)], { type: 'application/json' });

            if (file != undefined) {
                formData.append("file", file, file.name);
            }

            formData.append("data", payload);

            var xhr = new XMLHttpRequest();

            xhr.onreadystatechange = function () {
                console.log(xhr.readyState);
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        resolve(xhr.response);
                    } else {
                        reject(xhr.response)
                    }
                }
            }
            xhr.open("POST", this.baseUrl + this.SLASH + url, true);
            xhr.setRequestHeader("AUTH-TOKEN", 'Bearer ' + localStorage.getItem('auth-token'));

            xhr.send(formData);
        }));
    }
    postWithMultipleFile(url: string, files: Array<File>, model: any) {

        return Observable.fromPromise(new Promise((resolve, reject) => {

            var formData: FormData = new FormData();

            this.cleanDirtyRequest(model);
            var payload = new Blob([JSON.stringify(model)], { type: 'application/json' });

            if (files != undefined) {
                for (let file of files) {
                    formData.append("file", file, file.name);
                }
            }

            formData.append("data", payload);

            var xhr = new XMLHttpRequest();

            xhr.onreadystatechange = function () {
                console.log(xhr.readyState);
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        resolve(xhr.response);
                    } else {
                        reject(xhr.response)
                    }
                }
            }
            xhr.open("POST", this.baseUrl + this.SLASH + url, true);
            xhr.setRequestHeader("AUTH-TOKEN", 'Bearer ' + localStorage.getItem('auth-token'));

            xhr.send(formData);
        }));
    }


    postFile(url: string, file: any) {
        return Observable.fromPromise(new Promise((resolve, reject) => {

            var formData: FormData = new FormData();

            if (file != undefined) {
                formData.append("file", file, file.name);
            }

            var xhr = new XMLHttpRequest();

            xhr.onreadystatechange = function () {
                console.log(xhr.readyState);
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        resolve(xhr.response);
                    } else {
                        reject(xhr.response)
                    }
                }
            }
            xhr.open("POST", this.baseUrl + this.SLASH + url, true);
            xhr.setRequestHeader("Authorization", 'Bearer ' + localStorage.getItem('Authorization'));
            xhr.send(formData);
        }));
    }

    postFiles(url: string, files: Array<File>) {
        return Observable.fromPromise(new Promise((resolve, reject) => {

            var formData: FormData = new FormData();

            if (files != undefined) {
                for (let file of files) {
                    formData.append("file", file, file.name);
                }
            }

            var xhr = new XMLHttpRequest();

            xhr.onreadystatechange = function () {
                console.log(xhr.readyState);
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        resolve(xhr.response);
                    } else {
                        reject(xhr.response)
                    }
                }
            }
            xhr.open("POST", this.baseUrl + this.SLASH + url, true);
            xhr.setRequestHeader("Authorization", 'Bearer ' + localStorage.getItem('Authorization'));
            xhr.send(formData);
        }));
    }


    download(url: string, model: any, fileName: string, mime?: string): Promise<boolean> {
        return new Promise<boolean>((resolve, reject) => {
            if (mime == undefined) {
                mime = "application/pdf";
            }

            // Xhr creates new context so we need to create reference to this
            let self = this;
            var pending: boolean = true;
            this.cleanDirtyRequest(model);

            // Create the Xhr request object
            let xhr = new XMLHttpRequest();

            xhr.open('POST', this.baseUrl + this.SLASH + url, true);
            xhr.setRequestHeader("Authorization", 'Bearer ' + localStorage.getItem('Authorization'));
            xhr.setRequestHeader("Content-type", "application/json");
            xhr.responseType = 'blob';

            // Xhr callback when we get a result back
            // We are not using arrow function because we need the 'this' context
            xhr.onreadystatechange = function () {
                // If we get an HTTP status OK (200), save the file using fileSaver
                if (xhr.readyState === 4 && xhr.status === 200) {
                    var blob = new Blob([xhr.response], { type: mime });
                    saveAs(blob, fileName);
                }
                resolve(true)
            };

            // Start the Ajax request
            xhr.send(JSON.stringify(model));
        })

    }

    private createAuthorizationHeader() {
        // create authorization header with token
        let token = localStorage.getItem('Authorization');
        const headers = new Headers({ 'Authorization': 'Basic ' + token });
        const options = new RequestOptions({ headers: headers });
        return options;
    }

    //Download PDF

    downloadPdf(url: string, fileName: string) {
        let headers = new Headers();
        headers.append('Authorization', 'Bearer ' + localStorage.getItem('Authorization'));
        return this.http.get(this.baseUrl + this.SLASH + url, { headers: headers, responseType: ResponseContentType.Blob }).map(
            (res) => {
                console.log(res);
                // return new Blob([res.blob()], { type: 'application/pdf' })
                //let xhr = new XMLHttpRequest();
                //console.log(xhr);
                var blob = new Blob([res.blob()], { type: 'application/pdf' });
                saveAs(blob, fileName);
            })
    }

    //Multiselect Serilize/Deserilize
    convertArrayForMultiselect(data: Array<any>, id?: string, name?: string) {
        let tempArr = [];
        if (data != undefined && data.length > 0) {
            if (id == undefined) {
                id = "id";
            }
            if (name == undefined) {
                name = "name";
            }
            for (let entry of data) {
                tempArr.push({ "id": entry[id], "itemName": entry[name] });
            }
        }
        return tempArr;
    }

    convertForMultiselect(entry: any, id?: string, name?: string) {
        if (entry != undefined) {
            if (id == undefined) {
                id = "id";
            }
            if (name == undefined) {
                name = "name";
            }
            return [{ "id": entry[id], "itemName": entry[name] }];
        }
        return [];
    }

    handleMultiCombo(sms_dummy_div: ElementRef) {
        try {
            let el: HTMLElement = sms_dummy_div.nativeElement as HTMLElement;
            el.click();
        } catch (error) {

        }
    }

    handleUI() {
        // Collapse ibox function
        jQuery('.collapse-link').on('click', function () {
            var ibox = jQuery(this).closest('div.ibox');
            var button = jQuery(this).find('i');
            var content = ibox.children('.ibox-content');
            content.slideToggle(200);
            button.toggleClass('fa-chevron-up').toggleClass('fa-chevron-down');
            ibox.toggleClass('').toggleClass('border-bottom');
            setTimeout(function () {
                ibox.resize();
                ibox.find('[id^=map-]').resize();
            }, 50);
        });
    }
}



