import { Component, OnInit } from "@angular/core";
import { SmsService } from "../../SMS.service";
import { ChangePassword } from "./changepassword.model";
import { Router, ActivatedRoute } from "@angular/router";

declare var jQuery: any, toastr: any;

@Component({
    moduleId: module.id,
    selector: 'changepassword',
    templateUrl: 'changepassword.component.html'
})
export class ChangePasswordComponent implements OnInit {

    modal: ChangePassword = new ChangePassword();

    constructor(private service: SmsService, private router: Router) { }

    ngOnInit() {

    }

    change() {
        console.log(this.modal);

        this.service.post("user/changepassword", this.modal).subscribe(response => {
            console.log(response);
            if (response) {
                toastr.success('Password Changed successfully');
                this.reset();
                this.router.navigate(['login']);
            } else {
                toastr.error('Unable to Change Password');
                this.reset();
            }
        })
    }
    reset() {
        this.modal = new ChangePassword();
    }
}


