import { Component, OnInit } from '@angular/core';
import { SmsService } from "../../SMS.service";
import { Constant } from "../../constant";
import { searchModel } from "./search.model";
import { Attributes } from "./attr.model";
declare var jQuery: any, toastr: any;

@Component({
  selector: 'app-dashboard-one',
  templateUrl: './dashboard-one.component.html',
  styleUrls: ['./dashboard-one.component.css']
})
export class DashboardOneComponent implements OnInit {

  dropdownSettings = Constant.dropdownSettings2;

  modal: searchModel = new searchModel();
  attributes : Attributes = new Attributes();
  searchResponse : Array<any> = [];
  selectedGender: Array<any> = [];
  GenderResponse: Array<any> = [];

  selectedAge: Array<any> = [];
  ageResponse: Array<any> = [];

  selectedProductsBuy: Array<any> = [];
  productBuyResponse: Array<any> = [];

  selectedColos: Array<any> = [];
  colorResponse: Array<any> = [];

  selectedBuyingStyle: Array<any> = [];
  buyingStyleResponse: Array<any> = [];

  selectedcompatibility: Array<any> = [];
  compatibilityResponse: Array<any> = [];

  selectedEmail: Array<any> = [];
  emailResponse: Array<any> = [];

  selectedWorkingStyle: Array<any> = [];
  workingStyleResponse: Array<any> = [];

  selectedPNB: Array<any> = [];
  PNBResponse: Array<any> = [];

  selectedEmploymentAffinity: Array<any> = [];
  employmentAffinityResponse: Array<any> = [];

  selectedEmploymentNonAffinity: Array<any> = [];
  employmentNonAffinityResponse: Array<any> = [];

  selectedDominiant: Array<any> = [];
  DominiantResponse: Array<any> = [];



  loader: boolean = false;

  dashAgeResponse: Array<any> = [];
  dashGenderResponse: Array<any> = [];
  dashHouseholdResponse: Array<any> = [];
  dashSocialResponse: Array<any> = [];
  cognativeResponse: Array<any> = [];
  dropdownRresponse: Array<any> = [];

  TPNBResponse: Array<any> = [];
  TColorsResponse: Array<any> = [];
  TBSResponse: Array<any> = [];
  TPBResponse: Array<any> = [];
  TWSResponse: Array<any> = [];
  TESResponse: Array<any> = [];
  TCResponse: Array<any> = [];
  TENFResponse: Array<any> = [];
  TEFResponse: Array<any> = [];
  TDResponse: Array<any> = [];


  taskId: string;
  taskName: string;
  constructor(private service: SmsService) {
    this.taskId = localStorage.getItem("taskId");
    this.taskName = localStorage.getItem("taskName");
    console.log(this.taskId, this.taskName);
  }

  ngOnInit() {

    this.service.get("getAgeRangeMaxCount?taskId=" + this.taskId).subscribe(response => {
      this.dashAgeResponse = response;
    });
    this.service.get("getTopSocialAccount?taskId=" + this.taskId).subscribe(response => {
      this.dashSocialResponse = response;
    });
    this.service.get("getGenderMaxCount?taskId=" + this.taskId).subscribe(response => {
      this.dashGenderResponse = response;
    });
    this.service.get("getAllCognitiveData?taskId=" + this.taskId).subscribe(response => {
      this.cognativeResponse = response["cognitiveAttributes"];
      this.cognativeResponse.forEach(element => {
        if (element.attribute_name === "things_that_they_would_not_buy") {
          this.TPNBResponse = element.topAttrValues;
        }
        if (element.attribute_name === "dominant_attr") {
          this.TDResponse = element.topAttrValues;
        }
        if (element.attribute_name === "buyingPattern") {
          this.TBSResponse = element.topAttrValues;
        }
        if (element.attribute_name === "personality_style") {
          this.TWSResponse = element.topAttrValues;
        }
        if (element.attribute_name === "things_that_they_would_buy") {
          this.TPBResponse = element.topAttrValues;
        }
        if (element.attribute_name === "personalities_that_match_well") {
          this.TCResponse = element.topAttrValues;
        }
        if (element.attribute_name === "top_color_matches") {
          this.TColorsResponse = element.topAttrValues;
        }
        if (element.attribute_name === "employment_that_doesnt_fit_well") {
          this.TENFResponse = element.topAttrValues;
        }
        if (element.attribute_name === "employment_that_fits_well") {
          this.TEFResponse = element.topAttrValues;
        }
        if (element.attribute_name === "multiple_intelligence") {
          this.TESResponse = element.topAttrValues;
        }
      });
    });
    this.loadGrid();
  }

  loadGrid() {
    //filtered dropdown Api
    this.service.get("getFilterViewAttrs").subscribe(response => {
      this.dropdownRresponse = response;
      this.dropdownRresponse.forEach(element => {
        if (element.attrName === "top_color_matches") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.colorResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "things_that_they_would_not_buy") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.PNBResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "things_that_they_would_buy") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.productBuyResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "personality_style") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.workingStyleResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "personalities_that_match_well") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.emailResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "employment_that_fits_well") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.employmentAffinityResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "employment_that_doesnt_fit_well") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.employmentNonAffinityResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "dominant_attr") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.DominiantResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "buyingPattern") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.buyingStyleResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "age") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.ageResponse.push({ "id": i, "itemName": element });
          });
        }
        if (element.attrName === "gender") {
          let i = 0;
          element.attrValue.forEach(element => {
            this.GenderResponse.push({ "id": i, "itemName": element });
            i = i + 1;
          });
        }
        if (element.attrName === "multiple_intelligence") {
          let i = 0;
          element.attrValue.forEach(element => {
            i = i + 1;
            this.compatibilityResponse.push({ "id": i, "itemName": element });
          });
        }
      });
    });
  }

  gender : Array<any> = [];
  manageGender(isSelect, item: any) {
    if (isSelect) {
      this.gender.push(item["itemName"]);
      console.log(this.gender);
    } else {
      this.gender.splice(item["id"],1);
      console.log(this.gender);
    }
  }

  age : Array<any> = [];
  manageAge(isSelect, item: any) {
    if (isSelect) {
      this.age.push(item["itemName"]);
    } else {
      this.age.splice(item["id"],1);
    }
  }
  productBuy : Array<any> = [];
  manageProductBuy(isSelect, item: any) {
    if (isSelect) {
      this.productBuy.push(item["itemName"]);
    } else {
      this.productBuy.splice(item["id"],1);
    }
  }

  color : Array<any> = [];
  manageColor(isSelect, item: any) {
    if (isSelect) {
      this.color.push(item["itemName"]);
    } else {
      this.color.splice(item["id"],1);
    }
  }
  buyingStyle : Array<any> = [];
  manageBuyingStyle(isSelect, item: any) {
    if (isSelect) {

      this.buyingStyle.push(item["itemName"]);
    } else {
      this.buyingStyle.splice(item["id"],1);
    }
  }
  compatibility : Array<any> = [];
  managecompatibility(isSelect, item: any) {
    if (isSelect) {
      this.compatibility.push(item["itemName"]);
    } else {
      this.compatibility.splice(item["id"],1);
    }
  }

  email : Array<any> = [];
  manageEmail(isSelect, item: any) {
    if (isSelect) {
      this.compatibility.push(item["itemName"]);
    } else {
      this.compatibility.splice(item["id"],1);
    }
  }

  workingStyle : Array<any> = [];
  manageWorkingStyle(isSelect, item: any) {
    if (isSelect) {
      this.workingStyle.push(item["itemName"]);
    } else {
      this.workingStyle.splice(item["id"],1);
    }
  }
  
  productNotBuying : Array<any> = [];
  managePNB(isSelect, item: any) {
    if (isSelect) {
      this.productNotBuying.push(item["itemName"]);
    } else {
      this.productNotBuying.splice(item["id"],1);
    }
  }

  EmploymentAffinity  : Array<any> = [];
  manageEmploymentAffinity(isSelect, item: any) {
    if (isSelect) {
      this.EmploymentAffinity.push(item["itemName"]);
    } else {
      this.EmploymentAffinity.splice(item["id"],1);
    }
  }
  EmploymentNonAffinity : Array<any> = [];
  manageENA(isSelect, item: any) {
    if (isSelect) {
      this.EmploymentNonAffinity.push(item["itemName"]);
    } else {
      this.EmploymentNonAffinity.splice(item["id"],1);
    }
  }

  Dominiant : Array<any> = [];
  manageDominiant(isSelect, item: any) {
    if (isSelect) {
      this.compatibility.push(item["itemName"]);
    } else {
      this.compatibility.splice(item["id"],1);
    }
  }

  resetForm(){
    this.color = [];
    this.selectedColos = [];
    this.productBuy = [];
    this.selectedProductsBuy = [];
    this.productNotBuying = [];
    this.selectedPNB = [];
    this.workingStyle = [];
    this.selectedWorkingStyle = [];
    this.email = [];
    this.selectedEmail = [];
    this.compatibility = [];
    this.selectedcompatibility = [];
    this.EmploymentAffinity = [];
    this.selectedEmploymentAffinity = [];
    this.EmploymentNonAffinity = [];
    this.selectedEmploymentNonAffinity = [];
    this.Dominiant = [];
    this.selectedDominiant  = [];
    this.buyingStyle = [];
    this.selectedBuyingStyle = [];
    this.age = [];
    this.selectedAge = [];
    this.gender = [];    
    this.selectedGender = [];
  }

  Generate() {
    this.searchResponse = [];
    this.modal.taskId = this.taskId;    
    this.modal.attributes.push({"attr" : "top_color_matches", "value" : this.color.toString()});
    this.modal.attributes.push({"attr" : "things_that_they_would_not_buy", "value" : this.productNotBuying.toString()});
    this.modal.attributes.push({"attr" : "things_that_they_would_buy", "value" : this.productBuy.toString()});
    this.modal.attributes.push({"attr" : "personality_style", "value" : this.workingStyle.toString()});
    this.modal.attributes.push({"attr" : "personalities_that_match_well", "value" : this.email.toString()});
    this.modal.attributes.push({"attr" : "multiple_intelligence", "value" : this.compatibility.toString()});
    this.modal.attributes.push({"attr" : "employment_that_fits_well", "value" : this.EmploymentAffinity.toString()});
    this.modal.attributes.push({"attr" : "employment_that_doesnt_fit_well", "value" : this.EmploymentNonAffinity.toString()});
    this.modal.attributes.push({"attr" : "dominant_attr", "value" : this.Dominiant.toString()});
    this.modal.attributes.push({"attr" : "buyingPattern", "value" : this.buyingStyle.toString()});
    this.modal.attributes.push({"attr" : "age", "value" : this.age.toString()});
    this.modal.attributes.push({"attr" : "gender", "value" : this.gender.toString()});
    console.log(this.modal);

    this.service.post("getFilteredEmails", this.modal).subscribe(response => {
      this.searchResponse = response["emailList"];
      this.loader = true;
      toastr.success('Generated successfully');
      this.loader = false;
      this.resetForm();
      console.log(this.searchResponse);
  });
  }





}



