import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { SmsService } from "../SMS.service";
import { DomSanitizer } from '@angular/platform-browser';
import { Router, ActivatedRoute } from "@angular/router";
import * as jsPDF from 'jspdf'
import { EmailModel } from "./email.model";
declare var jQuery: any, toastr: any;
declare var $: any;

@Component({
    moduleId: module.id,
    selector: 'email-upload',
    templateUrl: 'email-upload.component.html',
    styleUrls: ['email-upload.component.css']

})
export class EmailUploadComponent implements OnInit {

    Emails: Array<any> = [];
    data: any;
    file: any;
    textEmails: string = "";
    error: boolean = false;
    selectEmails: boolean = true;
    uploaded: boolean = false;
    reportName: boolean = false;
    inputEmails: Array<any> = [];
    uploadEmails: Array<any> = [];
    emailModel: EmailModel = new EmailModel();
    TaskreportName: string;
    loader: boolean = false;
    taskId: string;
    taskName: string;
    constructor(private router: Router, private service: SmsService) { }

    ngOnInit() {
    }

    fileChanged(e) {
        this.file = e.target.files[0];
        this.uploadDocument(this.file);
    }

    uploadDocument(file) {
        let fileReader = new FileReader();
        fileReader.onload = (e) => {
            this.data = fileReader.result;
            this.uploadEmails = this.data.split("\n");
            for (let e of this.uploadEmails) {
                console.log(e);
                this.Emails.push(e);
            }
        }
        fileReader.readAsText(this.file);
    }

    onInputMails() {
        this.inputEmails = this.textEmails.split(",");
    }

    Remove(id) {
        console.log(id);
        this.Emails.splice(id, 1);
        if (this.Emails.length <= 0) {
            this.uploaded = false;
        }
    }
    next() {
        for (let e of this.inputEmails) {
            console.log(e);
            this.Emails.push(e);
        }
        if (this.Emails.length > 0) {
            this.selectEmails = false;
            this.uploaded = true;
            this.error = false;
        } else {
            this.error = true;
        }
        console.log(this.Emails);
    }

    Conform() {
        this.uploaded = false;
        this.error = false;
        this.reportName = true;
    }
    Submit() {
        this.loader = true;
        this.emailModel.taskName = this.TaskreportName;
        this.emailModel.emails = this.Emails;
        console.log(this.emailModel);
        this.service.post("addBatchEmails", this.emailModel).subscribe(response => {
            let taskId = response.taskId;
            let taskName = response.taskName;
            localStorage.setItem("taskId", taskId);
            localStorage.setItem("taskName", taskName);
            this.loader = false;
            toastr.success('Emails Uploaded successfully');
            this.router.navigate(['home/dashboardOne']);
        });
        // this.router.navigate(['home/dashboardOne']);
        console.log(this.Emails.toString());
    }

    Navigate(id) {
        if (id === 1) {
            this.selectEmails = true;
            this.uploaded = false;
            this.reportName = false;
        } else if (id === 2) {
            this.uploaded = true;
            this.selectEmails = false;
            this.reportName = false;
        }
    }
}


